* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

html, body {
    max-height: 100%;
    max-width: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

img {
    object-fit: cover;
}

.container {
    max-width: 1512px;
    margin: 0 auto;
    position: relative;
}